import MyNode from "./MyNode.js";
import {
    SVG
} from "@svgdotjs/svg.js";

export default class MyNodeButtonV2 extends MyNode {
    constructor(parent, network, state, allPath, name) {
        super(parent, network, state, allPath, name);
        const self = this;

        const html = self.svg.foreignObject(100, 50);
        html.add(SVG('<div style="position: fixed;"><button id="button1">Button</button></div>', true));
        html.move(50, 100);
        const button = document.getElementById("button1");
        button.addEventListener("click", function () {
            network.refresh();
            if (self.entrypointMethod) {
                self.execute(self.entrypointMethod, self.lang);
            } else {
                alert("You can no entrypoint!");
            }
        });
        button.addEventListener("mouseenter", function () {
            state.canDrag = false;
        });
        button.addEventListener("mouseleave", function () {
            state.canDrag = true;
        });
    }

    execute(entrypointMethod, lang) {
        this.next(this.outputs[0].id, entrypointMethod, lang);
    }

    setEntrypoint(entrypointMethod) {
        this.entrypointMethod = entrypointMethod;
    }

    setLang(lang) {
        this.lang = lang;
    }
}